body {
  --color-text-hover: #00acdc;
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  margin-left: 50px;
}

.nav {
  margin-right: 50px;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
  position: relative;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  color: #000;
  text-transform: uppercase;
  font-family: Roboto, serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav__link:hover, .is-active {
  color: var(--color-text-hover);
}

.is-active:after {
  content: "";
  background-color: var(--color-text-hover);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
